// ÐœÐ°ÑÑÐ¸Ð² Ñ†Ð²ÐµÑ‚Ð¾Ð² Ð² hex-Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚Ðµ
const colors = [
  '#FFFFFF',
  '#2196F3',
  '#4CAF50',
  '#FF9800',
  '#009688',
  '#795548',
];

const CHANGE_COLORS_DELAY = 1000;
let timeoutId = null;
let isActive = false;
const refs = {
  body: document.querySelector('body'),
  start: document.querySelector('button[data-action="start"]'),
  stop: document.querySelector('button[data-action="stop"]'),
};

// Ð”Ð»Ñ Ð³ÐµÐ½ÐµÑ€Ð°Ñ†Ð¸Ð¸ ÑÐ»ÑƒÑ‡Ð°Ð¹Ð½Ð¾Ð³Ð¾ Ñ‡Ð¸ÑÐ»Ð° (Ð¸Ð½Ð´ÐµÐºÑ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ð¼Ð°ÑÑÐ¸Ð²Ð° Ñ†Ð²ÐµÑ‚Ð¾Ð²)
const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

refs.start.addEventListener('click', onStartClick);
refs.stop.addEventListener('click', onStopClick);

function onStartClick() {
  if (isActive) {
    return;
  }
  timeoutId = setInterval(addBgColor, CHANGE_COLORS_DELAY);
  isActive = true;
}

function onStopClick() {
  clearTimeout(timeoutId);
  isActive = false;
}

function addBgColor() {
  refs.body.style.cssText = `background-color: ${newColor()}`;
}

function newColor() {
  const idxColor = randomIntegerFromInterval(0, colors.length - 1);
  return colors[idxColor];
}


